.Reasons{
    padding: 0 2rem ;
    display: flex;
    gap:2rem;
} 
.reasons-left{
    /* flex: 1 1; */
    display: grid;
    grid-template-columns:repeat(3,1fr);
    gap: 1rem;
    grid-auto-rows: 1fr;
    
}

.reasons-left>img{
    object-fit: cover;
}

 .reasons-left>:nth-child(1){
     height: 28rem;
    width: 12rem;
    grid-row:1/3 ;
} 

.reasons-left>:nth-child(2){
    width: auto;
    height: 16rem;
    grid-column: 2/4;
}
.reasons-left>:nth-child(3){
    width: 14rem;
    grid-column: 2/3;
    grid-row: 2;
}
.reasons-left>:nth-child(4){
    width: 10rem;
    grid-row: 2;
    grid-column: 3/4;
}

 .reasons-right{
    flex: 1 1;
    display: flex;
    text-transform: uppercase;
    flex-direction: column;
    gap: 1rem;
} 
.reasons-right>span{
    color: orange;
    font-weight: bold;
}
.reasons-right>div{
    font-size: 3rem;
    color: white;
    font-weight: bold;
}
.detailsr{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.detailsr > div{
    display: flex;
    gap: 1 rem;
    font-size: 1rem;
}
.detailsr>div>img{
    width: 2rem;
    height: 2rem;
}
.partners{
    display: flex;
    gap: 1 rem;

}
.partners>img{
    width: 2.5rem;
    padding: 0.3rem;
}